* @Last Modified time: 2018-07-31 13:46:33
-->
<template>
    <div class="page page-current">
        <header class="bar bar-nav">
            <h1 class="title"></h1>
        </header>
        <bottomBar></bottomBar>
        <div class="content gray">
            <div class="pf-wrap">
                <div class="img-wrap"> <img :src="userInfo.sex==='female'?'/static/image/female.jpg':'/static/image/male.jpg'" alt=""></div>
                <div class="inner">
                    <p>{{hidePhoneFun(userInfo.phone)}}</p>
                    <div class="attr-item">
                        <span class="attr-name"></span>
                        <span class="attr-val">{{userInfo.alitm||'--'}}</span>
                    </div>
                </div>
                <router-link to="/auth" class="authBtn"> <i class="icon icon-right"></i></router-link>
            </div>
            <div class="list-block">
                <ul>
                    <li class="item-content item-link">
                        <router-link to="/uc/flow">
                            <div class="item-media"><i class="icon icon-flow"></i></div>
                            <div class="item-inner">
                                <div class="item-title"></div>
                                <div class="item-after"></div>
                            </div>
                        </router-link>
                    </li>
                    <li class="item-content item-link">
                        <router-link to="/uc/coin">
                            <div class="item-media"><i class="icon icon-coin"></i></div>
                            <div class="item-inner">
                                <div class="item-title"></div>
                                <div class="item-after">{{userInfo.coin}}</div>
                            </div>
                        </router-link>
                    </li>
                    <li class="item-content item-link" v-if="userInfo.userRole==='business'">
                        <router-link to="/uc/points">
                            <div class="item-media"><i class="icon icon-points"></i></div>
                            <div class="item-inner">
                                <div class="item-title"></div>
                                <div class="item-after">{{userInfo.integral}}</div>
                            </div>
                        </router-link>
                    </li>
                </ul>
            </div>
            <div class="list-block">
                <ul>
                    <li class="item-content item-link">
                        <router-link to="/uc/concat">
                            <div class="item-media"><i class="icon icon-tel"></i></div>
                            <div class="item-inner">
                                <div class="item-title"></div>
                                <div class="item-after"></div>
                            </div>
                        </router-link>
                    </li>
                    <li style="margin-top: 2rem;">
                        <div class="submitWrap"><a href="javascript:;" @click.stop="logoutFun" class="button button-big button-fill button-danger"></a></div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
import bottomBar from '@/components/bottomBar';
import {
    hidePhone
} from '@/javascript/utils';

let $ = window.$;
export default {
    name: 'userCenterIndex',
    data() {
        return {
            userInfo: this.$store.state.userInfo
        };
    },
    created() {},
    methods: {
        /**
         * @Author      weiberZeng
         * @DateTime    2018-07-31
         * @lastTime    2018-07-31
         * @description 
         */
        hidePhoneFun(val) {
            return hidePhone(val);
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-07-31
         * @lastTime    2018-07-31
         * @description 
         */
        logoutFun() {
            this.$http.get('/user/logout').then((response) => {
                if (response.data.success) {
                    // 
                    localStorage.setItem('userInfo', '');
                    this.$store.state.userInfo = {};
                    // 
                    this.$router.replace({
                        path: '/account/login'
                    });
                } else {
                    $.alert(response.data.message);
                }
            });
        }
    },
    components: {
        bottomBar
    }
};

</script>



